/* loading bar */

.loading-bar {
    width: 100%;
    height: 40px;
    background: var(--linear-blue);
    animation: loading 2s linear;
    animation-iteration-count: infinite;
    border-radius: 5px;

    &__container {
        height: 90vh;
        margin-top: 30px;
    }
}

@keyframes loading {
    0% {
        width: 0%;
    }

    100% {
        width: 100%;
    }
}